<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <!--      导航-->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <!--      header 头部-->
      <div class="header">
        <div class="dividing-line"></div>
      </div>
      <!--主要区域  "向他购买"-->
      <div class="main">
        <div class="title clear-fix">
          <div class="left fl">投票上币规则</div>
          <div class="right fr" @click="toList()">我的参与 <img class="img" src="../../assets/image/youjiantou.png" alt=""> </div>
        </div>
        <!-- 列表区域 循环此区域-->
        <div v-for="(item,index) in voteListssss" :key="index" class="list-item" :class="index%2==0?'deep':''">
          <!--          标题栏-->
          <div class="list-nav-box">
            <div class="list-nav-left">
              <span class="left-icon"><img src="../../assets/image/user.jpg" alt="" style="width: 100%;height: 100%;border-radius: 100%;"></span>
              <span style="margin-left: 0.27rem;color: rgba(255,255,255,0.5);">{{item.currency_id}}  {{item.title}}</span>
            </div>
            <span class="right-btn" @click="showVent(item.least, item.maximum, item.status, item.consume_number, item.x_currency_id, item.activity_id)">
              {{ item.status == 1 ? '购买' : '已下架' }}
            </span>
          </div>
          <!--          单价栏-->
          <div  class="list-price-box1">
            <div class="list-price-left">
              <span class="left-text">{{item.support}}票</span>
            </div>
            <!-- {{( Number(item.support) / (Number(item.support)+Number(item.oppose)) *100)}} -->
            <div class="process-bar" style="margin-top: 0.2rem;flex: 1;">
              <!-- :style="{ width: ( Number(item.support) / (Number(item.support)+Number(item.oppose)) *100)+'%'}" -->
              <!-- style="width:21.51%" -->
                <!-- <div class="process-bar-hover"
                 :style="{ width: speed(item.support, item.oppose)+'%'}" ></div> -->
                  <div class="process-bar-hover" style="width:21.51%" ></div>
            </div>
            <div class="" style="color:#E9435A">
              <span class="left-text" style="color:#E9435A">{{item.oppose}}票</span>
            </div>
          </div>
          <div   class="list-num-box">
            <div class="list-num-left">
              <span class="left-text left">支持票数</span>
            </div>
            <div class="list-num-left">
              <span class="left-text centent">总票数：{{Number(item.support)+Number(item.oppose)}}</span>
            </div>
            <div class="list-num-left">
              <span class="left-text right">反对票数</span>
            </div>
          </div>
          <!--  -->
        </div>
      </div>
      <!-- 点击购买弹窗 -->
      <div class="posiBox"  v-if="isShow" @click="showBox()"></div>
      <div class="ventanal"  v-if="isShow">
        <div class="title">立即参与</div>
        <div class="isTips">
          <span class="num">投票数最少: <span style="color:#3DD078">{{least}}</span></span>
          <span class="nums">投票数最多:<span style="color:#FA4C45">{{maximum}}</span></span>
        </div>
        <div class="iptBox">
          <span class="text">证件类型:</span>
          <div style="flex: 1;" class="rideoBox">
            <van-radio-group v-model="addCoin.is_status" direction="horizontal">
            <van-radio name="1">支持</van-radio>
            <van-radio name="2">反对</van-radio>
          </van-radio-group>
          </div>
        </div>
        <div class="iptBox">
          <span class="text">投票数量</span>
          <input class="ipt" type="number" v-model="addCoin.number" placeholder="请输入数量">
        </div>
        <div class="iptBox">
          <span class="text">交易密码</span>
          <input class="ipt" type="password" v-model="addCoin.paypass" placeholder="请输入交易密码">
        </div>
        <div class="tipsText">
          <p style="line-height: 0.6rem;">*每投一票燃烧 {{consume_number}} {{x_currency_id}}，从账户自动扣除</p>
          <p >*投票不可退票</p>
        </div>
        <div></div>
        <div class="subBox" @click="userSub()">提交</div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Toast, RadioGroup, Radio } from 'vant'
// import { votess } from '@/api/user'
import { votess, addvote } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
Vue.use(Toast).use(RadioGroup).use(Radio)

export default {
  components: { NavHead },
  data () {
    return {
      url: this.GLOBAL.domainName,
      parameter: {
        activity: 'vote'
      },
      voteListssss: [],
      addCoin: {
        u_id: '2', // 用户ID
        activity: 'vote', // 固定值（newcoin）
        activity_id: '', // 活动ID
        is_status: '1',
        number: '', // 购买数量
        paypass: '' // 交易密码
      },
      curHeight: '',
      isShow: false,
      least: '1', // 最少投票数
      maximum: '1000', // 最少投票数
      setTime: '',
      consume_number: '0.1', // 消耗币种
      x_currency_id: 'BTC', // 支付币种
      voteVal: 0, // 进度条
      title: '投票上币'
    }
  },
  created () {
    this.addCoin.u_id = sessionStorage.getItem('u_id')
    this.voteList()
    this.beforeMount()
  },
  methods: {
    showBox () {
      this.isShow = false
    },
    voteList () {
      var that = this
      var data = objKeySort(this.parameter)
      var token = sgin(data)
      this.parameter.token = token
      votess(this.parameter).then(res => {
        console.log(res, that, '投票列表')
        if (res.code === 0) {
          that.voteListssss = res.data
        }
      }).catch((err) => {
        console.log(err, that)
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    showVent (least, maximum, status, consume, currencyid, activityid) {
      var that = this
      console.log('点击了')
      if (status === '1') {
        that.least = least
        that.maximum = maximum
        that.isShow = true
        that.consume_number = consume
        that.x_currency_id = currencyid
        that.addCoin.activity_id = activityid
      } else {
        that.$toast({
          message: '活动已下架',
          duration: 1500
        })
      }
    },
    speed (support, oppose) {
      this.voteVal = 0
      var support1 = Number(support)
      var oppose1 = Number(oppose)
      var total = support1 + oppose1
      if (support1 === 0) {
        this.voteVal = 50
      } else {
        this.voteVal = (support1 / total) * 100
      }
      // console.log(this.voteVal)
      return this.voteVal
    },
    userSub () {
      var that = this
      console.log(that.addCoin.is_status, that.addCoin, 'that.addCoin.is_status')
      if (that.addCoin.number === '') {
        that.$toast({
          message: '投票数量不能为空',
          duration: 1500
        })
        return
      }
      if (Number(that.addCoin.least) >= Number(that.addCoin.number)) {
        var msg = '购买数量不能少于' + parseInt(that.addCoin.least)
        that.$toast({
          message: msg,
          duration: 1500
        })
        return
      }
      if (this.addCoin.paypass === '') {
        that.$toast({
          message: '交易密码不能为空',
          duration: 1500
        })
        return
      }
      var data = objKeySort(this.addCoin)
      var token = sgin(data)
      this.addCoin.token = token
      addvote(this.addCoin).then(res => {
        console.log(res, that, '交易成功')
        if (res.code === 0) {
          that.$toast({
            message: '投票成功',
            duration: 1500
          })
          that.isShow = false
          that.voteList(that.parameter)
          that.addCoin.number = ''
          that.addCoin.paypass = ''
          delete this.addCoin.token
          // Vue.$delete(this.addCoin, 'token')
          // console.log('投票成功删除对象', this.addCoin)
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        that.isShow = false
        that.addCoin.number = ''
        that.addCoin.paypass = ''
        delete this.addCoin.token
      })
      this.isShow = false
    },
    toList () {
      this.$router.push({
        path: '/vote/list',
        query: {
          // id: id
        }
      })
    }
  }
}
</script>
